<!--
 * @Author Demon3443002624@qq.com
 * @Version 2022-5-18 14:43:51
 * @Description 操作记录
 * @Version 2022-5-18 14:44:14
 * @Description 
-->
<template>
  <div>
    <BMainTopComponent />
    <el-container>
      <el-main>
        <div id="crew-record-operating">
          <!-- <toggle-crew ></toggle-crew> -->
          <el-card shadow="never">
            <div class="block">
              <span>筛选：</span>
              <el-date-picker
                v-model="value_date"
                type="daterange"
                range-separator="至"
                start-placeholder="开始时间"
                end-placeholder="结束时间"
                format="yyyy-MM-dd"
                :popper-append-to-body="false"
                placement="bottom-start"
              >
              </el-date-picker>
            </div>
            <el-row type="flex" align="middle">
              &nbsp;&nbsp;&nbsp;&nbsp;类型：
              <el-select v-model="value_type" placeholder="请选择">
                <el-option v-for="(v, k) in typeOpt" :key="k" :label="v" :value="k"> </el-option>
              </el-select>
            </el-row>
            <el-button type="primary" @click="_query">搜索</el-button>
          </el-card>
          <el-card shadow="never">
            <table-list
              :options="options"
              :columns="columns"
              :dataSource="dataSource"
              :dataTotal="dataTotal"
              :pagination="paginationCfg"
              @handleChangePage="handleChangePage"
              @handleChangeSize="handleChangeSize"
            />
          </el-card>
        </div>
      </el-main>
      <toggle-crew ></toggle-crew>
    </el-container>
  </div>
</template>

<script>
import dayjs from 'dayjs'
import { mapGetters } from 'vuex'

// import _data from '../data'
import { formatTime } from '@/utils/common'
import ToggleCrew from '@/components/toggleCrew/list'

// 表格头设置
const columns = [
  {
    label: '类型',
    prop: 'type',
    props: {
      // sortable: 'sortable'
    }
  },
  // 20220518 周三 Demon 隐藏内容
  // {
  //   label: '内容',
  //   prop: 'content',
  //   align: 'left'
  // },
  {
    label: '操作人',
    prop: 'operating_people'
  },
  {
    label: '操作时间',
    prop: 'operating_time'
  }
]
export default {
  name: `CrewRecordOperating`,
  components: {
    ToggleCrew
  },
  data() {
    return {
      columns,
      dataSource: [],
      dataTotal: 0,
      options: {
        index: true,
        labelIndex: '序号'
      },
      paginationCfg: {
        currentPage: 1,
        pageSize: 10,
        layout: 'total, sizes, prev, pager, next, jumper',
        pageSizes: [10, 20, 30, 40]
      },
      data: [],
      value_date: [new Date(new Date().setDate(new Date().getDate() - 7)), new Date()],
      value_type: '0',
      queryOpt: {},
      typeOpt: {
        // -1
        0: '全部',
        1: '停机按键',
        2: '手动按键',
        3: '模拟按键',
        4: '自动按键',
        5: '静音按键',
        6: '开机按键',
        7: '发电合闸',
        8: '市电合闸',
        9: '报警复位',
        10: '发电分闸',
        11: '市电分闸'
      }
    }
  },
  watch: {
    selectCrew: {
      handler: function (nv) {
        this._query()
      }
    }
  },
  created() {
    this.queryOpt = {
      value_date: this.value_date,
      value_type: this.value_type
    }
  },
  computed: {
    ...mapGetters(['selectCrew'])
  },
  mounted() {
    this._query()
  },
  methods: {
    // 查询数据
    queryTableData() {
      const q = {
          pageSize: this.paginationCfg.pageSize,
          page: this.paginationCfg.currentPage
        },
        p = {
          // DateStart: dayjs(this.value_date[0]).format('YYYY-MM-DD'),
          // DateEnd: dayjs(this.value_date[1]).format('YYYY-MM-DD'),
          // Type: Number(this.value_type) || -1,
          DateStart: dayjs(this.queryOpt.value_date[0]).format('YYYY-MM-DD'),
          DateEnd: dayjs(this.queryOpt.value_date[1]).format('YYYY-MM-DD'),
          Type: Number(this.queryOpt.value_type) || -1,
          GeneratorSetId: this.selectCrew.ID
        }
      this.$api.equipment
        .getOperateRecords(p, q)
        .then((value) => {
          // value = _data.operating
          const dataObj = !!value && !!value.Data && value.Data
          if (dataObj) {
            const arr = []
            const dataList = dataObj.Data
            !dataList ||
              dataList.length === 0 ||
              dataList.forEach((e, i) => {
                const obj = {
                  key: i,
                  type: this.typeOpt[e.Type],
                  content: e.Content,
                  operating_people: e.UserName,
                  operating_time: formatTime(e.Creare_Time).format('YYYY-MM-DD HH:mm:ss')
                }
                arr.push(obj)
              })
            this.dataTotal = dataObj.TotalItemCount
            this.$set(this, 'dataSource', arr)
          }
        })
        .catch(() => {})
    },
    handleNewJump(row) {},
    // 分页 当前页改变
    handleChangePage(val) {
      this.paginationCfg.currentPage = val
      this.queryTableData()
    },
    // 分页 每页条数改变
    handleChangeSize(val) {
      this.paginationCfg.currentPage = 1
      this.paginationCfg.pageSize = val
      this.queryTableData()
    },
    // 搜索
    _query() {
      this.paginationCfg.currentPage = 1
      this.queryOpt = {
        value_date: this.value_date,
        value_type: this.value_type
      }
      this.queryTableData()
    }
  }
}
</script>

<style lang="less" scoped>
#crew-record-operating {
  /deep/.el-card {
    overflow: initial;
    margin-bottom: 15px;
    .el-card__body {
      display: flex;
      .el-button {
        margin: 0 0 0 10px;
      }
      .el-input__inner {
        height: 32px;
      }
    }
  }
  /deep/ .toggle-crew {
    margin-bottom: 0;
  }
}
</style>



